.featured-projects {
  padding: clamp(3rem, 5vw, 5%) clamp(3%, 8%, 8%);
  width: 100%;
  font-family: var(--font-main);
}

.featured-projects h1 {
  font-size: 5rem;
  color: var(--primary);
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
  letter-spacing: -0.03em;
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
}

.project-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  background: rgba(29, 104, 242, 0.03);
  padding: clamp(2.5rem, 4vw, 4rem);
  border-radius: 16px;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  gap: clamp(3rem, 4vw, 4rem);
  border: 2px solid rgba(29, 104, 242, 0.3);
  box-shadow: 
    0 0 0 2px rgba(29, 104, 242, 0.15),
    0 8px 30px rgba(29, 104, 242, 0.1),
    inset 0 0 30px rgba(29, 104, 242, 0.02);
}

.project-card:nth-child(even) {
  flex-direction: row-reverse;
}

.project-card:hover {
  transform: translateY(-5px);
  border-color: rgba(29, 104, 242, 0.5);
  background: rgba(29, 104, 242, 0.06);
  box-shadow: 
    0 0 0 2px rgba(29, 104, 242, 0.25),
    0 12px 40px rgba(29, 104, 242, 0.15),
    inset 0 0 40px rgba(29, 104, 242, 0.04);
}

.project-info {
  position: relative;
  z-index: 10;
  width: 60%;
  text-align: left;
}

.project-card:nth-child(even) .project-info {
  text-align: right;
}

.project-info h2 {
  font-size: 1.8rem;
  color: var(--primary);
  font-weight: 600;
  margin-bottom: 1rem;
  letter-spacing: -0.02em;
}

.project-info p {
  font-size: 1.2rem;
  color: var(--text-primary);
  margin: 1.5rem 0;
  line-height: 1.6;
  letter-spacing: -0.01em;
}

.tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.8rem;
  margin: 1.5rem 0;
}

.project-card:nth-child(even) .tech-stack {
  justify-content: flex-end;
}

.tech {
  background: rgba(29, 104, 242, 0.1);
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 1rem;
  color: var(--primary);
  font-weight: 500;
  transition: all 0.3s ease;
  border: 1px solid rgba(29, 104, 242, 0.3);
}

.tech:hover {
  background: rgba(29, 104, 242, 0.2);
  border-color: rgba(29, 104, 242, 0.4);
}

.project-links {
  margin-top: 1.5rem;
  display: flex;
  gap: 1rem;
}

.project-card:nth-child(even) .project-links {
  justify-content: flex-end;
}

.project-link {
  color: var(--text-primary);
  font-size: 1.5rem;
  text-decoration: none;
  transition: all 0.3s ease;
  padding: 0.5rem;
}

.project-link:hover {
  color: var(--primary);
  transform: translateY(-2px);
}

.project-image-container {
  position: relative;
  width: 40%;
  border-radius: 12px;
  overflow: hidden;
}

.project-image {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 12px;
  transition: transform 0.3s ease;
}

.project-card:hover .project-image {
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .featured-projects h1 {
    font-size: 3.5rem;
  }

  .project-card,
  .project-card:nth-child(even) {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    padding: 1.5rem;
  }

  .project-info,
  .project-card:nth-child(even) .project-info {
    width: 100%;
    text-align: center;
  }

  .project-info h2 {
    font-size: 1.6rem;
  }

  .project-info p {
    font-size: 1.1rem;
  }

  .tech-stack,
  .project-card:nth-child(even) .tech-stack {
    justify-content: center;
  }

  .project-links,
  .project-card:nth-child(even) .project-links {
    justify-content: center;
  }

  .project-image-container {
    width: 100%;
  }
}

.theme-toggle {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px;
  border-radius: 50%;
  border: none;
  background: transparent;
  cursor: pointer;
  color: inherit;
  transition: transform 0.3s;
}

.theme-toggle:hover {
  transform: scale(1.1);
}
