.experience-container {
  padding: 1rem 5%;
}

.experience-container h2 {
  font-size: 5rem;
  color: #1d68f2;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 700;
}

.experience-timeline {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.experience-card {
  background: rgba(29, 104, 242, 0.05);
  border-radius: 12px;
  padding: 1.2rem;
  transition: all 0.3s ease;
}

.experience-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(29, 104, 242, 0.1);
}

.experience-header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 1rem;
  align-items: center;
  margin-bottom: 0.8rem;
}

.company-logo {
  width: 40px;
  height: 40px;
  object-fit: contain;
  border-radius: 8px;
}

.title-company {
  display: flex;
  flex-direction: column;
}

.experience-header h3 {
  color: #1d68f2;
  font-size: 1.8rem;
  margin: 0;
  font-weight: 600;
}

.company-link {
  font-size: 1.3rem;
  color: #ffffff;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.3s ease;
}

.company-link:hover {
  color: #1d68f2;
}

.experience-details {
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
  color: #bbbbbb;
  font-size: 1.1rem;
  text-align: right;
}

.detail {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  justify-content: flex-end;
}

.detail-icon {
  color: #1d68f2;
}

.experience-description {
  display: grid;
  grid-template-columns: 1fr;
}

.experience-description ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 0.3rem;
}

.experience-description li {
  color: #ffffff;
  font-size: 1.2rem;
  padding-left: 1rem;
  position: relative;
  line-height: 1.3;
}

.experience-description li::before {
  content: "•";
  color: #1d68f2;
  position: absolute;
  left: 0;
}

.experience-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 0.8rem;
  align-items: center;
}

.skill-tag {
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 1rem;
  font-weight: 500;
  color: #1d68f2;
}

@media (max-width: 768px) {
  .experience-container {
    padding: 0.8rem;
  }
  
  .experience-card {
    padding: 0.8rem;
  }
  
  .company-logo {
    width: 30px;
    height: 30px;
  }
  
  .experience-header h3 {
    font-size: 1.4rem;
  }
  
  .company-link {
    font-size: 0.9rem;
  }
  
  .experience-details {
    flex-direction: column;
    gap: 0.3rem;
  }
  
  .experience-header {
    grid-template-columns: auto 1fr;
  }
  
  .experience-details {
    grid-column: 1 / -1;
    text-align: left;
    margin-top: 0.5rem;
  }
  
  .detail {
    justify-content: flex-start;
  }
  
  .experience-container h2 {
    font-size: 3.5rem;
  }
  
  .experience-details {
    font-size: 1rem;
  }
  
  .experience-description li {
    font-size: 1.1rem;
  }
  
  .skill-tag {
    font-size: 0.95rem;
  }
}
