/* Main container for the Header section */
.header {
  position: relative;
  width: 100%;
  height: 100vh;
  color: white;
  text-align: left;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 0 5%;
  background: #000000;
  overflow: hidden;
}

/* Right side custom design */
.header .design-right {
  position: absolute;
  top: 0;
  right: 0;
  width: 40%;
  height: 100%;
  background: linear-gradient(135deg, #ff7a00, #ff0077);
  z-index: -1;
  opacity: 0.7;
  border-radius: 0 50% 50% 0;
}

/* Header content container */
.header-content {
  max-width: 800px;
  width: 100%;
  text-align: left;
  z-index: 1;
}

/* Main title */
.header-content h1 {
  margin: 0;
  font-size: 6rem;
  font-weight: 700;
  text-align: left;
  white-space: nowrap;
  overflow: visible;
}

/* Animated text styling */
.header-content h1 .animated-text {
  background: linear-gradient(45deg, #ff7a00, #ff0077, #1da1f2);
  background-size: 200% auto;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradient 3s linear infinite;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Cursor animation */
.cursor {
  display: inline-block;
  width: 10px;
  height: 2rem;
  background-color: white;
  margin-left: 5px;
  opacity: 0;
}

.cursor.visible {
  opacity: 1;
}

/* Subtitle */
.header-content p {
  margin: 0.5rem 0 0;
  font-size: 2rem;
  font-weight: 400;
  color: #bbbbbb;
  text-align: left;
}

/* Social icons container */
.icons {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  justify-content: flex-start;
}

/* Individual icon */
.icon {
  color: white;
  font-size: 2.5rem;
  transition: color 0.3s ease, transform 0.3s ease;
}

.icon:hover {
  color: #1da1f2;
  transform: translateY(-3px);
}

/* Media Queries */
@media (max-width: 1200px) {
  .header {
    height: 90vh;
  }

  .header-content h1 {
    font-size: 5rem;
  }
  
  .header-content p {
    font-size: 1.8rem;
  }
}

@media (max-width: 992px) {
  .header {
    height: 80vh;
  }

  .header-content h1 {
    font-size: 4.5rem;
  }
  
  .header-content p {
    font-size: 1.6rem;
  }
}

@media (max-width: 768px) {
  .header {
    height: 70vh;
    padding: 2rem;
  }

  .header-content h1 {
    font-size: 4rem;
  }

  .header-content p {
    font-size: 1.4rem;
  }

  .header .design-right {
    width: 100%;
    height: 40%;
    bottom: 0;
    top: auto;
    border-radius: 50% 50% 0 0;
  }
}

@media (max-width: 480px) {
  .header {
    height: 60vh;
    padding: 1.5rem;
  }

  .header-content h1 {
    font-size: 3rem;
  }
  
  .header-content p {
    font-size: 1.2rem;
  }

  .icons {
    gap: 0.8rem;
  }

  .icon {
    font-size: 1.8rem;
  }
}

/* Handle landscape mode on mobile */
@media (max-height: 480px) {
  .header {
    height: auto;
    min-height: 100vh;
    padding: 2rem 1rem;
  }

  .header-content h1 {
    font-size: 2.5rem;
  }
  
  .header-content p {
    font-size: 1.1rem;
  }

  .icons {
    margin-top: 0.5rem;
  }
}
