/* src/Skills.css */

/* General styling for the section */
.skills {
  background-color: #000000; /* Light background for consistency */
  padding: 5% 10%;
  border-radius: 15px;
  margin-top: 3rem;
  width: 100%;
  max-width: 100%; /* Ensure full width */
  text-align: left;
  margin-left: 0; /* Ensure full-width */
  margin-right: 0; /* Ensure full-width */
  font-family: 'Inter', sans-serif; /* Consistent font */
}

/* Heading style */
.skills h2 {
  font-size: 4rem;
  color: #1d68f2; /* Dark text color */
  font-weight: 700;
  margin-bottom: 2rem; /* Adjust this margin if needed */
  text-align: center;
  font-family: 'Roboto', sans-serif; /* Consistent font for headings */
  border: none; /* Ensure there's no border */
  padding-bottom: 0; /* Remove any padding that might cause the line */
}


/* Flexbox for the skills section */
.skills-section {
  display: flex;
  flex-wrap: wrap; /* Allow categories to wrap */
  gap: 2rem; /* Space between categories */
  justify-content: space-between; /* Distribute categories across screen */
}

.skills-category {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  width: 30%; /* Each category takes 30% of screen */
}

.skills-category h3 {
  font-size: 1.8rem;
  color: #1d68f2;
  margin-bottom: 1rem;
  font-weight: 600;
  width: 100%;
  text-align: left;
}

.skills-category ul {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start; /* Align skills to the left */
  gap: 1rem;
}

.skills-category li {
  font-size: 1.1rem;
  color: #2b2d42;
  padding: 0.8rem 1.2rem;
  border: 2px solid #2b2d42;
  border-radius: 25px;
  background-color: #ffffff;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
  text-align: center;
  font-weight: 500;
}

.skills-category li:hover {
  background-color: #2b2d42;
  color: #ffffff;
}

/* Link styling inside list items */
.skills-category li a {
  text-decoration: none;
  color: #2b2d42;
  font-weight: 600;
  display: block;
  padding: 5px 10px;
}

.skills-category li:hover a {
  color: #ffffff;
}

/* Media Queries for responsiveness */
@media (max-width: 1024px) {
  .skills-category {
    width: 45%; /* Adjust category width for medium screens */
  }
}

@media (max-width: 768px) {
  .skills-category {
    width: 100%; /* Stack categories on small screens */
  }
}

.skills-container {
  padding: clamp(2rem, 4vw, 4rem) 0;
  background-color: #000000;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skills-container h2 {
  font-size: 4rem;
  color: #1d68f2;
  margin-bottom: 3rem;
  text-align: center;
  font-weight: 700;
}

.skills-grid-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 3rem;
  width: 100%;
  max-width: 1200px;
  padding: 0 2rem;
}

.skills-category {
  width: 100%;
}

.skills-category h3 {
  font-size: 1.8rem;
  color: #00b4d8;
  margin-bottom: 1.5rem;
  border-bottom: 2px solid #1d68f2;
  padding-bottom: 0.5rem;
  font-weight: 600;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(100px, 1fr));
  gap: 1rem;
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: rgba(29, 104, 242, 0.1);
  border-radius: 8px;
  padding: 1rem;
  transition: all 0.3s ease;
}

.skill-item:hover {
  transform: translateY(-5px);
  background: rgba(29, 104, 242, 0.15);
  box-shadow: 0 4px 15px rgba(29, 104, 242, 0.2);
}

.skill-icon {
  font-size: 2rem;
  color: #1d68f2;
  margin-bottom: 0.5rem;
  transition: all 0.3s ease;
}

.skill-item:hover .skill-icon {
  color: #00b4d8;
  transform: scale(1.1);
}

.skill-name {
  font-size: 0.9rem;
  color: #ffffff;
  text-align: center;
  font-weight: 500;
}

@media (max-width: 992px) {
  .skills-grid-container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

@media (max-width: 768px) {
  .skills-container h2 {
    font-size: 3rem;
    margin-bottom: 2rem;
  }

  .skills-category h3 {
    font-size: 1.5rem;
  }
}

@media (max-width: 480px) {
  .skills-container h2 {
    font-size: 2.5rem;
  }

  .skills-grid {
    grid-template-columns: repeat(auto-fill, minmax(80px, 1fr));
    gap: 0.8rem;
  }

  .skill-item {
    padding: 0.8rem;
  }

  .skill-icon {
    font-size: 1.5rem;
  }

  .skill-name {
    font-size: 0.8rem;
  }
}

/* Ensure smooth transitions */
* {
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
