/* Main container for the About Me section */
.about-me {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 90vh;
  padding: clamp(2rem, 4vw, 4rem) clamp(3%, 5%, 5%);
  background: var(--bg-dark);
  color: var(--text-primary);
  font-family: var(--font-main);
  flex-wrap: wrap;
  gap: clamp(2rem, 3vw, 3rem);
}

/* Left side with profile image */
.left-side {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.profile-pic {
  width: clamp(250px, 30vw, 500px);
  height: auto;
  border-radius: 12px;
  object-fit: cover;
}

/* Styling for the image caption */
.image-caption {
  text-align: center;
  font-size: var(--text-base);
  color: var(--text-secondary);
  font-weight: 500;
  margin-top: 1rem;
  letter-spacing: -0.02em;
}

/* Right side with description */
.right-side {
  flex: 1;
  padding-left: 2rem;
  text-align: left;
}

/* Title styling */
.right-side h2 {
  font-size: 5rem;
  color: var(--primary);
  font-weight: 700;
  margin: 0;
  margin-bottom: clamp(1.5rem, 2vw, 2rem);
  letter-spacing: -0.03em;
}

/* Body text styling */
.right-side p {
  font-size: var(--text-xl);
  margin-bottom: clamp(1rem, 1.5vw, 1.5rem);
  line-height: 1.6;
  color: var(--text-primary);
  letter-spacing: -0.02em;
}

/* Read More Button Styling */
.read-more-btn {
  background: transparent;
  border: 2px solid var(--primary);
  color: var(--primary);
  padding: 1.2rem 3rem;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: var(--font-main);
  letter-spacing: -0.02em;
  margin-top: 2rem;
  min-width: 180px;
  display: inline-block;
}

.read-more-btn:hover {
  background: var(--primary);
  color: white;
  transform: translateY(-2px);
}

/* Media Queries for responsiveness */
@media (max-width: 1200px) {
  .right-side h2 {
    font-size: 4rem;
  }
}

@media (max-width: 1100px) {
  .about-me {
    flex-direction: column;
    text-align: center;
    height: auto;
    padding: 2rem 5%;
  }

  .left-side {
    margin-bottom: 2rem;
  }

  .profile-pic {
    width: 80%;
    max-width: 300px;
  }

  .right-side {
    padding-left: 0;
  }

  .right-side h2 {
    font-size: 3.5rem;
  }

  .right-side p {
    font-size: var(--text-lg);
    text-align: center;
  }
}

/* Hide the navigation dots */
.fp-right {
  display: none !important;
}
