.about-me-detailed {
  background: var(--bg-dark);
  color: var(--text-primary);
  font-family: var(--font-main);
  min-height: 100vh;
  padding-top: 100px;
}

.timeline-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 5%;
}

.about-me-detailed h1 {
  font-size: 5rem;
  color: var(--primary);
  text-align: center;
  margin-bottom: 4rem;
  font-weight: 700;
  letter-spacing: -0.03em;
}

.timeline {
  position: relative;
  padding: 2rem 0;
}

.timeline::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 4px;
  height: calc(100% - 100px);
  background: var(--primary);
  opacity: 0.2;
  border-radius: 2px;
}

.timeline-item {
  display: flex;
  justify-content: center;
  padding: 3rem 0;
  opacity: 0;
  animation: fadeIn 0.5s ease forwards;
  animation-delay: calc(var(--index, 0) * 0.1s);
}

.timeline-date {
  width: 250px;
  padding-right: 4rem;
  text-align: right;
  font-size: 1.3rem;
  color: var(--primary);
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 1rem;
  opacity: 0.9;
}

.timeline-icon {
  font-size: 1.8rem;
  color: var(--primary);
}

.timeline-content {
  width: 50%;
  padding-left: 4rem;
  position: relative;
  background: rgba(29, 104, 242, 0.03);
  padding: 2rem;
  border-radius: 12px;
  border: 2px solid rgba(29, 104, 242, 0.1);
  transition: all 0.3s ease;
}

.timeline-content:hover {
  transform: translateY(-5px);
  border-color: rgba(29, 104, 242, 0.3);
  background: rgba(29, 104, 242, 0.05);
}

.timeline-content::before {
  content: '';
  position: absolute;
  left: -10px;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background: var(--primary);
  border-radius: 50%;
  border: 4px solid var(--bg-dark);
  box-shadow: 0 0 0 4px rgba(29, 104, 242, 0.1);
}

.timeline-content h3 {
  font-size: 2rem;
  color: var(--primary);
  margin-bottom: 1rem;
  font-weight: 600;
  letter-spacing: -0.02em;
}

.timeline-content p {
  font-size: 1.1rem;
  line-height: 1.6;
  color: var(--text-primary);
  letter-spacing: -0.01em;
  opacity: 0.9;
}

.nav-buttons {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 3rem;
}

.nav-buttons.bottom {
  margin-top: 4rem;
  margin-bottom: 2rem;
}

.back-button {
  display: inline-block;
  color: var(--primary);
  font-size: 1.2rem;
  text-decoration: none;
  padding: 1rem 2rem;
  border: 2px solid var(--primary);
  border-radius: 8px;
  transition: all 0.3s ease;
  background: rgba(29, 104, 242, 0.05);
}

.back-button:hover {
  background: var(--primary);
  color: white;
  transform: translateY(-2px);
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@media (max-width: 1024px) {
  .timeline-content {
    width: 60%;
  }
  
  .timeline-date {
    width: 200px;
    padding-right: 3rem;
  }
}

@media (max-width: 768px) {
  .about-me-detailed h1 {
    font-size: 3.5rem;
    margin-bottom: 3rem;
  }

  .timeline::before {
    left: 20px;
  }

  .timeline-item {
    flex-direction: column;
    padding: 2rem 0;
  }

  .timeline-date {
    width: 100%;
    text-align: left;
    padding: 0 0 1rem 3.5rem;
    font-size: 1.2rem;
  }

  .timeline-content {
    width: calc(100% - 40px);
    margin-left: 40px;
    padding: 1.5rem;
  }

  .timeline-content::before {
    left: -30px;
  }

  .timeline-content h3 {
    font-size: 1.6rem;
  }

  .timeline-content p {
    font-size: 1rem;
  }

  .nav-buttons {
    justify-content: center;
    margin-bottom: 2rem;
  }

  .nav-buttons.bottom {
    margin-top: 3rem;
  }
} 