/* Import Space Grotesk font */
@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');

/* Define a color palette using CSS variables for easy customization */
:root {
  --primary: #1d68f2;
  --primary-light: rgba(29, 104, 242, 0.1);
  --primary-hover: #1554cc;
  --accent: #00b4d8;
  --text-primary: #ffffff;
  --text-secondary: #bbbbbb;
  --bg-dark: #000000;
  --navbar-height: 80px;
  
  /* Typography */
  --font-main: 'Space Grotesk', sans-serif;
  --text-xs: clamp(0.75rem, 0.9vw, 0.875rem);
  --text-sm: clamp(0.875rem, 1vw, 1rem);
  --text-base: clamp(1rem, 1.1vw, 1.125rem);
  --text-lg: clamp(1.125rem, 1.2vw, 1.25rem);
  --text-xl: clamp(1.25rem, 1.5vw, 1.5rem);
  --text-2xl: clamp(1.5rem, 2vw, 2rem);
  --text-3xl: clamp(2rem, 3vw, 3rem);
  --text-4xl: clamp(2.5rem, 4vw, 4rem);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: var(--font-main);
}

body {
  background-color: var(--bg-dark);
  color: var(--text-primary);
  min-height: 100vh;
  width: 100%;
  overflow-x: hidden;
  font-weight: 400;
  letter-spacing: -0.02em;
  line-height: 1.6;
}

.App {
  min-height: 100vh;
  width: 100%;
  background-color: var(--bg-dark);
}

/* Compact sections */
section {
  min-height: auto; /* Remove forced full height */
  width: 100%;
  background: none;
  border: none;
  margin: 0;
  padding: 20px 0; /* Reduced padding */
}

.container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 2rem;
  background: none;
}

/* Tighter heading margins */
h1, h2, h3, h4, h5, h6 {
  border: none;
  margin-bottom: 10px; /* Reduced from 20px */
}

/* Remove ALL backgrounds */
header, 
section, 
.navbar, 
.skills-container,
.experience-container,
.featured-projects,
.about-me,
.footer {
  background: none !important;
  background-color: transparent !important;
}

/* Tighter spacing for text elements */
h1 {
  color: var(--primary);
  font-size: 2.5em;
  text-align: center;
  margin-bottom: 10px; /* Reduced */
}

h2 {
  margin-bottom: 8px; /* Reduced */
  font-size: 1.8em;
  color: var(--text-color);
  padding-bottom: 3px; /* Reduced */
}

h3 {
  font-size: 1.3em;
  margin-top: 10px; /* Reduced */
}

/* Tighter list spacing */
ul {
  padding-left: 15px;
}

ul li {
  margin: 4px 0; /* Reduced from 8px */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  section {
    padding: 15px 0; /* Even tighter on mobile */
  }

  .container {
    padding: 0 1rem; /* Reduced side padding */
  }

  h1 { font-size: 2em; }
  h2 { font-size: 1.5em; }
  h3 { font-size: 1.2em; }

  .App {
    margin: 0;
    padding: 0;
  }
}

/* Add to your existing App.css */
.scroll-indicator {
  position: fixed;
  right: 30px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.scroll-dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: all 0.3s ease;
}

.scroll-dot.active {
  background: var(--primary);
  transform: scale(1.4);
}

.scroll-dot:hover {
  background: var(--primary);
}

/* Add smooth scrolling to the whole page */
html {
  scroll-behavior: smooth;
}

/* Typography styles */
h1, h2, h3, h4, h5, h6 {
  font-weight: 600;
  letter-spacing: -0.03em;
  line-height: 1.2;
}

h1 {
  font-size: var(--text-4xl);
  font-weight: 700;
}

h2 {
  font-size: var(--text-3xl);
  color: var(--primary);
}

h3 {
  font-size: var(--text-2xl);
}

p {
  font-size: var(--text-base);
  line-height: 1.7;
  color: var(--text-secondary);
}

.section-title {
  font-size: var(--text-4xl);
  font-weight: 700;
  letter-spacing: -0.04em;
  margin-bottom: 1.5rem;
}

/* Button text */
button {
  font-family: var(--font-main);
  font-weight: 500;
  letter-spacing: -0.01em;
}

/* Link text */
a {
  font-family: var(--font-main);
  font-weight: 500;
  text-decoration: none;
  transition: color 0.3s ease;
}

/* List text */
li {
  font-size: var(--text-base);
  line-height: 1.6;
}

/* Small text */
.text-sm {
  font-size: var(--text-sm);
}

/* Large text */
.text-lg {
  font-size: var(--text-lg);
}

/* Extra large text */
.text-xl {
  font-size: var(--text-xl);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  :root {
    --text-4xl: clamp(2rem, 3vw, 3rem);
    --text-3xl: clamp(1.75rem, 2.5vw, 2.5rem);
    --text-2xl: clamp(1.5rem, 2vw, 2rem);
  }
}

/* Add to your existing App.css */
.section-transition {
  position: relative;
  overflow: hidden;
}

/* Fade in animation for sections */
.section-transition {
  opacity: 0;
  transform: translateY(30px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.section-transition.visible {
  opacity: 1;
  transform: translateY(0);
}

/* Remove the old section-transition gradient */
.section-transition::after {
  display: none; /* Remove the old line */
}

/* Enhanced section divider */
.section-divider {
  position: relative;
  width: 300px; /* Slightly wider */
  height: 2px;
  margin: 40px auto;
  background: transparent;
  overflow: visible;
}

.section-divider::before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 2px;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(29, 104, 242, 1) 20%, /* Full opacity */
    rgba(0, 180, 216, 1) 80%, /* Full opacity */
    transparent
  );
  transform: translateY(-50%);
  opacity: 0.9;
  box-shadow: 
    0 0 20px rgba(29, 104, 242, 0.6),
    0 0 40px rgba(29, 104, 242, 0.3);
}

.section-divider::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: 8px; /* Slightly larger dot */
  height: 8px;
  background: #ffffff;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  opacity: 1;
  box-shadow: 
    0 0 15px var(--primary),
    0 0 30px var(--accent);
}
